#columnVideo {
  overflow: hidden;
  width: 100%;
  padding-top: 30px;
  position: relative;
  padding-bottom: 80px;
}
#columnVideo .statusBox {
  padding-right: 30px;
  box-sizing: border-box;
}
#columnVideo .articleEditBox {
  background: #eceff4;
  padding: 8px 20px;
  position: relative;
}
#columnVideo .articleEditBox span {
  background: #ffffff;
  border: 1px solid #c3c5ce;
  text-align: center;
  padding: 6px 8px;
  display: inline-block;
  font-size: 16px;
  color: #333333;
  cursor: pointer;
  margin-right: 16px;
}
#columnVideo .articleEditBox .status-active {
  background: #f06e65;
  border: 1px solid #e94035;
  color: #fff;
  margin-right: 16px;
}
#columnVideo .articleEditBox .add {
  position: absolute;
  right: 10px;
  top: 50%;
  transform: translateY(-50%);
  width: 100px;
  color: #E46D64;
  border: 1px solid #c3c5ce;
}
#columnVideo .list {
  padding: 20px 0;
  box-sizing: border-box;
}
#columnVideo .list .flexBox {
  display: flex;
  align-items: center;
}
#columnVideo .list .flexBox .flex {
  flex: 1;
}
#columnVideo .list .list_head,
#columnVideo .list .list_info {
  padding: 10px 0;
  padding-left: 16px;
  box-sizing: border-box;
  border-bottom: 1px solid #f0f0f0;
}
#columnVideo .list .list_head .num,
#columnVideo .list .list_info .num,
#columnVideo .list .list_head .name,
#columnVideo .list .list_info .name,
#columnVideo .list .list_head .status,
#columnVideo .list .list_info .status {
  width: 19%;
  color: #333;
  font-weight: 600;
}
#columnVideo .list .list_head .operation,
#columnVideo .list .list_info .operation {
  color: #333;
  font-weight: 600;
}
#columnVideo .list .list_info .num,
#columnVideo .list .list_info .name,
#columnVideo .list .list_info .status,
#columnVideo .list .list_info .operation {
  font-weight: normal;
}
#columnVideo .list .list_info .operation a,
#columnVideo .list .list_info .operation > span,
#columnVideo .list .list_info .operation > div {
  display: inline-block;
  background: #f0f0f0;
  border-radius: 2px;
  padding: 6px 8px;
  margin-right: 10px;
  cursor: pointer;
  color: #666666;
  font-size: 14px;
}
#columnVideo .list .list_info .operation a span,
#columnVideo .list .list_info .operation > span span,
#columnVideo .list .list_info .operation > div span {
  padding: 0;
  margin-right: 0;
}
#columnVideo .pagination {
  display: flex;
  padding: 10px 0;
  align-content: center;
  justify-content: center;
  position: absolute;
  bottom: 20px;
  width: 100%;
  left: 0;
}
#columnVideo .pagination span {
  display: inline-block;
  padding: 8px;
  cursor: pointer;
}
